.header {
  height: 50px;
  width: 100%;
  display: flex;
  padding: 20px 60px;
  justify-content: space-between;
  margin-bottom: 10px;

  .logo-container {
    height: 100%;
    width: 50px;
    padding-top: 5px;
  }

  .nav {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    .nav-menu {
      padding: 5px 15px;
      cursor: pointer;
    }
  }
}
